<template>
  <nav class="navbar navbar-dark bg-dark" aria-label="First navbar example">
    <div class="container-fluid d-flex">
      <div class="flex-fill d-flex justify-content-center align-items-center">
       <button class="btn d-flex" @click="redirectToMap(isMap)">
         <i v-if="!isMap" class="far fa-map text-white fs-2"></i>
         <i v-else class="fas fa-home text-white fs-2"></i>
       </button>
      </div>
      <div class="flex-fill">
        <a class="navbar-brand mx-0" @click="setHomePage();">1944 Monte Cassino</a>
      </div>
      <div class="flex-fill" v-if="!isMap">
        <button class="navbar-toggler collapsed border-0 px-0" type="button" data-bs-toggle="collapse"
                data-bs-target="#navbarsExample01" aria-controls="navbarsExample01" aria-expanded="false"
                aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
      </div>
      <div class="navbar-collapse collapse vh-100 mobileMenu" id="navbarsExample01">
        <ul class="navbar-nav me-auto mb-2 d-flex h-100 ">
          <li v-for="(page,key) in pagesNavbar" :key="key" class="nav-item text-white flex-fill d-flex justify-content-center align-items-center">
            <a v-smooth-scroll :href="page.link" @click="removeShow()">
              {{page.title}}
              </a>
          </li>
        </ul>

      </div>
    </div>
  </nav>
</template>
<script>
export default {
  props:['pagesNavbar'],
  data() {
    return {
      isMap: false
    }
  },
  methods: {
    redirectToMap(value) {
      this.isMap = !value;
      this.$store.commit('setMobileMap',this.isMap );
      this.runPreloader();
    },
    removeShow(){
      let $ = require('jquery');
      $('.mobileMenu').removeClass('show');
    },
    setHomePage(){
      if(this.isMap){
        this.$store.commit('setMobileMap',false );
        this.runPreloader();
      }
    },
    runPreloader() {
      let loader = this.$loading.show(
              {
                backgroundColor: '#000',
                opacity: 1,
                color: '#fff',
                loader: 'dots'
              }
      );
      setTimeout(() => {
        loader.hide();
      }, 1000);
    }
  },
  watch: {
    '$store.state.isMobileMap': function (isMobile) {
      this.isMap = isMobile
    },
  }
}
</script>
<style scoped>
.mobileMenu a {
  text-decoration: none;
  color:white;
}
.navbar-toggler {
  border-color: transparent !important;
}
</style>